3;\n    font-size: 16px !important;\n    padding: 18px;\n\n    &.is-active {\n      font-weight: bold;\n    }\n  }\n}\n\n:deep() .el-collapse {\n  border: none;\n\n  .el-collapse-item {\n    padding: 0 16px;\n\n    > div {\n      border-radius: 4px;\n    }\n\n    &:not(:first-child) {\n      margin-top: 16px;\n    }\n\n    > div:first-child {\n      background: #f3f3f3;\n    }\n  }\n\n  .el-collapse-item__header {\n    justify-content: flex-end;\n    flex-direction: row-reverse;\n    border: 0 !important;\n    background: transparent;\n    border-radius: 4px;\n    padding: 0 16px;\n    height: 32px;\n    font-weight: 700;\n    font-size: 14px !important;\n\n    > .el-collapse-item__arrow {\n      margin: 0 8px 0 0;\n    }\n  }\n\n  .el-collapse-item__wrap {\n    padding-top: 16px;\n    border: none;\n  }\n}\n\n:deep() .el-collapse .el-collapse-item__content {\n  padding-bottom: 0px;\n\n  .el-collapse-item__header {\n    font-weight: 500;\n    background: #f3f3f3;\n    font-size: 12px !important;\n  }\n}\n\n:deep() .el-collapse-item__content > .el-form-item:last-child {\n  margin-bottom: 0 !important;\n}\n\n:deep() .coll-title > .el-collapse-item {\n  padding: 0;\n\n  > div {\n    > .el-collapse-item__header {\n      > i {\n        font-weight: 700;\n      }\n    }\n\n    &:last-child {\n      > .el-collapse-item__content {\n        padding-bottom: 0px !important;\n      }\n    }\n  }\n\n  > div:first-child {\n    background: transparent;\n\n    > .el-collapse-item__header {\n      background: transparent;\n    }\n\n    > div:first-child {\n      font-size: 14px !important;\n      color: #333333;\n      font-weight: 700;\n    }\n  }\n}\n\n.h5-content {\n  flex: 1;\n  overflow: auto;\n}\n\n/* 修改滚动条轨道 */\n::-webkit-scrollbar {\n  width: 0px;\n  /* 滚动条宽度 */\n}\n\n/* 修改滚动条滑块 */\n::-webkit-scrollbar-thumb {\n  /* 滑块颜色 */\n  border-radius: 2px;\n  /* 滑块圆角 */\n}\n\n/* 修改滚动条轨道背景 */\n::-webkit-scrollbar-track {\n  background: transparent;\n  /* 轨道背景颜色 */\n}\n\n/* 修改滚动条边框 */\n::-webkit-scrollbar-thumb {\n  border: none;\n  /* 滑块边框 */\n}\n\n.main-content {\n  height: 800px;\n  width: 100%;\n  display: flex;\n\n  .model {\n    display: flex;\n    flex-direction: column;\n    width: 100px;\n    box-shadow: 3px 3px 8px 0px rgba(225, 225, 225, 0.5);\n    background: #ffffff;\n    padding: 32px;\n\n    > .el-button {\n      &:hover {\n        background: rgba(75, 130, 250, 0.1);\n        border: 1px solid rgba(193, 213, 255, 1);\n        color: #4b82fa;\n      }\n\n      justify-content: flex-start;\n      margin-bottom: 16px;\n      margin-left: 0;\n    }\n\n    .action-page {\n      background: rgba(75, 130, 250, 0.1);\n      border: 1px solid rgba(193, 213, 255, 1);\n      color: #4b82fa;\n      border-radius: 4px;\n    }\n  }\n}\n\n.mu-content {\n  height: 800px;\n  flex: 1;\n  display: flex;\n\n  .mu-right {\n    background: #ffffff;\n    // box-shadow: 3px 0px 8px 0px rgba(225, 225, 225, 0.5);\n    border-radius: 0px 0px 12px 0px;\n    width: 46%;\n    overflow: auto;\n\n    :deep(.el-tabs--border-card > .el-tabs__content) {\n      padding: 16px !important;\n      .el-form {\n        overflow: hidden;\n      }\n    }\n\n    :deep(.el-collapse-item__header) {\n    }\n  }\n\n  .mu-left {\n    width: 54%;\n    height: 800px;\n    background: #f4f4f4;\n    display: flex;\n\n    .left-page {\n      width: 375px;\n      background: #fff;\n      height: 667px;\n      margin: auto;\n      margin-top: 32px;\n    }\n\n    .left-h5 {\n      display: flex;\n      flex-direction: column;\n    }\n\n    .newYearBg {\n      background: url(\"@/assets/h5/newYearBg.jpg\") !important;\n      background-size: 100% 100%;\n    }\n\n    .video-play-header {\n      background: url(\"@/assets/h5/videoPlayHeader.png\") no-repeat !important;\n      background-size: 100% 100%;\n    }\n  }\n}\n\n.block {\n  width: 50%;\n}\n\n.avatar-uploader :deep(.el-upload) {\n  border: 1px dashed var(--el-border-color);\n  border-radius: 6px;\n  cursor: pointer;\n  position: relative;\n  overflow: hidden;\n  transition: var(--el-transition-duration-fast);\n\n  img {\n    width: 80px;\n    height: 80px;\n    object-fit: cover;\n  }\n\n  &:hover {\n    border-color: var(--el-color-primary);\n  }\n\n  .el-icon.avatar-uploader-icon {\n    font-size: 28px;\n    color: #8c939d;\n    width: 80px;\n    height: 80px;\n    text-align: center;\n  }\n}\n\n:deep() .vc-color-wrap {\n  border-radius: 4px;\n}\n\n:deep() .el-collapse {\n  .el-icon {\n    font-family: \"iconfont\" !important;\n  }\n\n  .el-collapse-item__arrow {\n    transform: rotate(180deg) scale(0.4);\n\n    &:before {\n      content: \"\\e69d\";\n    }\n\n    &.is-active {\n      transform: rotate(0deg) scale(0.4) translateY(-2px);\n    }\n\n    > svg {\n      display: none;\n    }\n  }\n}\n\n.modal-header {\n  width: 100%;\n  height: 30px;\n  align-items: center;\n  display: flex;\n  justify-content: space-between;\n\n  :deep(.el-button) {\n    height: 28px;\n    width: 72px !important;\n  }\n\n  .button-box {\n    // width: 120px;\n    display: flex;\n    justify-content: space-between;\n\n    :deep() .el-loading-spinner {\n      margin-top: -8px;\n      svg {\n        width: 16px !important;\n        height: 16px !important;\n      }\n    }\n  }\n\n  .el-button + .el-button {\n    margin-left: 16px;\n  }\n}\n\n:deep() .el-tab-pane {\n  overflow: auto;\n  max-height: 720px;\n}\n"],"sourceRoot":""}]);
